let tkdata = null;

const prettyDomain = function (url) {
    if (!(url && /^https?:\/\//.test(url)))
        return null;
    const segments = url.split('/');
    if (segments.length > 4 || segments[3]) // path and other stuff
        return null;
    let domain = segments[2]
    if (domain.includes(':')) // port
        return null;
    //domain = domain.replace(/^www\./, '');
    return domain.toLowerCase();
};

