const default_icon = function(cls) {
    if (cls.title == 'Student')
        return 'female';
    if (cls.title == 'Ansatt')
        return 'user';
    return 'user';
}

export default async function() {
    if (!tkdata) {
        const res = await fetch(process.env.VUE_APP_BACKEND_URL);
        tkdata = await res.json()

        const user = (uid) => {
            return {uid, name: (tkdata.user[uid] || uid + '@uib.no')};
        };

        Object.values(tkdata.service).forEach(svc => {
            // does the service have it's own domain?
            svc.link.domain = prettyDomain(svc.link.home);

            // expand info values
            Object.keys(info_formatted).forEach(k => {
                if (svc.info[k] !== undefined) {
                    svc.info[k + '_formatted'] = info_formatted[k][svc.info[k]] || (k + ':' + svc.info[k]);
                }
            });

            // expand users
            svc.meta.author = user(svc.meta.author);
            if (svc.role) {
                if (svc.role.owner)
                    svc.role.owner = user(svc.role.owner);
                if (svc.role.operator)
                    svc.role.operator = svc.role.operator.split(' ').map(user);
            }

            // expand support
            if (svc.related && svc.related.support_contact) {
                svc.related.support_contact = tkdata.support[svc.related.support_contact.id];
            }

            // expand classification
            const classes = [];
            if (svc.classification) {
                svc.classification.split(' ').forEach(c => {
                    const cls = tkdata.class[c];
                    if (!cls.icon)
                        cls.icon = default_icon(cls);
                    classes.push(cls);
                });
            }
            classes.sort((a,b) => b.type.localeCompare(a.type) || a.weight - b.weight || a < b);
            svc.classification = classes;

            // expand flag into object of booleans - that's easier to test for
            if (svc.flag) {
                const flag = {}
                svc.flag.split(' ').forEach(f => {
                    flag[f] = true;
                })
                svc.flag = flag;
            }
            else {
                svc.flag = {}
            }
        });
    }
    return tkdata;
}