<template>
    <section v-if="services.length">
        <div class="buttons">
            <button @click="toggleFilterPromoted()" title="Vis/skjul fremhevede tjenester">{{filterPromoted ? 'Anbefalt' : 'Alle'}}</button>
            <button @click="orderByTitle()" title="Velg sorteringsrekkefølge">{{orderBy == 'z-a' ? "Å–A" : "A–Å"}}</button>
            <button @click="orderByMTime()" v-if="bus.ita_mode" title="Sort etter tidspunkt for endringer i oppføringen"><fa-icon name="clock-o"/></button>
            <button @click="showSummary = !showSummary" title="Vis/skjul ekstra informasjon om tjenestene">
                <fa-icon name="angle-down" v-if="showSummary"/>
                <fa-icon name="angle-right" v-else/>
            </button>
            <input v-model="filter" type="text" placeholder="Søk etter tjenester" >
            <button :disabled="!filter.length"><fa-icon name="search"/></button>
        </div>
        <template v-if="filteredServices.length">
            <h1>Tjenester (<template v-if="filteredServices.length!=visibleServices.length">{{filteredServices.length}}/</template>{{visibleServices.length}}):</h1>
            <transition-group name="tklist" tag="ul">
                <li v-for="svc in filteredServices" :key="svc.id" :class="svcClasses(svc)">
                    <router-link :to="'/node/' + svc.id">{{svc.title}}
                    &#32;<span class="domain-link" v-if="svc.domain"><a :href="svc.link">{{svc.domain}}</a></span>
                    &#32;<span class="weight" v-if="bus.ita_mode && orderBy=='weight' && svc.weight">v={{svc.weight}}</span>
                    &#32;<span v-if="orderBy=='mtime'" class="changed"><fa-icon name="clock-o"/>&nbsp;{{svc.changed.substr(0,10)}}</span>
                    <a v-if="svc.link" class="svc-link" :href="svc.link" title="Besøk tjenesten"><fa-icon name="external-link"/></a>
                    <div v-if="showSummary && svc.classification.length" class="tags">
                        <div class="tag" :class="'tag_' + cls.type" v-for="cls in svc.classification" :key="cls.id">
                            <fa-icon :name="cls.icon" v-if="cls.icon"/>
                            {{cls.title}}
                        </div>
                    </div>
                    <p class="summary" v-if="showSummary && svc.summary">{{svc.summary}}</p>
                    </router-link>
                </li>
            </transition-group>
        </template>
        <p class="err" v-else>
            Ingen tjenester funnet ved å søke på "{{filter}}".
        </p>
    </section>
    <section v-else>
        <p class="loading">Loading...</p>
    </section>
</template>

<script>
import FaIcon from '@/components/FaIcon.vue';
import { eventBus } from '@/main';
import tkdata from '@/tkdata';

export default {
    props: {
        showSummary: {type: Boolean, default: false}
    },
    data() {
        return {
            services: [],
            orderBy: "a-z",   // "a-z", "z-a", "mtime", "weight"
            filter: "",
            filterPromoted: !eventBus.ita_mode,
            filterPromotedToggled: false,
            bus: eventBus,
        }
    },
    created() {
        tkdata().then(data => {
            const lst = [];
            Object.values(data.service).forEach(svc => {
                lst.push({
                    id: svc.id,
                    title: svc.title.trim(),
                    summary: svc.text.summary,
                    link: svc.link.home,
                    domain: svc.link.domain,
                    state: svc.info.service_state,
                    type: svc.info.service_type,
                    changed: svc.meta.changed,
                    flag: svc.flag,
                    classification: svc.classification,
                    weight: 0,
                });
            });
            this.services = lst;
        });
    },
    computed: {
        visibleServices() {
            if (this.bus.ita_mode)
                return this.services;
            else
                return this.services.filter(svc => svc.state != 'avsluttet' && svc.type == 'cfs');
        },
        filteredServices() {
            const filter = this.filter.trim().toLowerCase()
            let services = this.visibleServices;
            services.forEach(svc => {
                svc.weight = 0;
                if (filter.length) {
                    const domain = svc.domain || '';
                    const title = svc.title.toLowerCase();
                    const summary = svc.summary.toLowerCase();

                    if (domain.startsWith(filter))
                        svc.weight += 1;
                    if (title.startsWith(filter))
                        svc.weight += 1;

                    if (filter.length > 1) {
                        if (title.includes(filter))
                            svc.weight += 1;
                        if (summary.includes(filter))
                            svc.weight += 0.5;
                    }
                }
            });
            this.sortServices(services);
            if (filter)
                services = services.filter(svc => svc.weight > 0);
            if (this.filterPromoted)
                services = services.filter(svc => svc.flag.promote);
            return services;
        }
    },
    watch: {
        'filter': function() {
            this.orderBy = 'weight';
            if (!this.filterPromotedToggled)
                this.filterPromoted = false;
        }
    },
    methods: {
        toggleFilterPromoted() {
            this.filterPromoted = !this.filterPromoted;
            this.filterPromotedToggled = true;
        },
        sortServices(services) {
            if (!services)
                services = this.services;
            if (this.orderBy == 'a-z') {
                services.sort((a,b) => a.title.localeCompare(b.title, 'nb'));
            }
            else if (this.orderBy == 'z-a') {
                services.sort((a,b) => b.title.localeCompare(a.title, 'nb'));
            }
            else if (this.orderBy == 'mtime') {
                services.sort((b,a) => a.changed.localeCompare(b.changed, 'nb'));
            }
            else { // orderBy 'weight'
                services.sort((a,b) => b.weight - a.weight || a.title.localeCompare(b.title, 'nb'));
            }
        },
        sortByTime() {
            this.show_changed = true;
            this.services.sort((b,a) => a.changed.localeCompare(b.changed, 'nb'));
        },
        orderByTitle() {
            if (this.orderBy == 'a-z')
                this.orderBy = 'z-a';
            else
                this.orderBy = 'a-z'
        },
        orderByMTime() {
            this.orderBy = 'mtime';
        },
        svcClasses(svc) {
            let cls = {clearfix: true, ...svc.flag}
            if (this.filterPromoted)
                cls['promote'] = false;
            cls['state-' + svc.state] = true;
            cls['type-' + svc.type] = true;
            return cls;
        }
    },
    components: {
        FaIcon,
    }
}
</script>

